* {
    padding: 0px;
    margin: 0px;
}

.parent_wrapper {
    background-color: white;
}

.parent_wrapper_orange {
    /* background-color: rgb(208, 187, 148); */
    background-color: rgb(226,205,182);
}

#content_wrapper {
    padding-top: 3.3vmax;
    padding-bottom: 3.3vmax;
    padding-right: 8vw;
    padding-left: 8vw;
    justify-content: center;
    background-color: white;
    margin-left: auto;
    margin-right: auto;
    max-width: 1600px;
}

#content_wrapper_orange {
    padding-top: 3.3vmax;
    padding-bottom: 3.3vmax;
    padding-right: 8vw;
    padding-left: 8vw;
    justify-content: center;
    /* background-color: rgb(208, 187, 148); */
    background-color: rgb(226,205,182);
    margin-left: auto;
    margin-right: auto;
    max-width: 1600px;
}

.special-max-width {
    max-width: 1000px !important;
}